<template>
    <section>
        <div id="map"></div>
        <div id="mapInfo">
            <ul>
                <li v-for="(news, index) in newsData" :key="index">
                    <router-link v-bind:to="news.userUrl">{{ news.userName }}</router-link>{{ news.str }} {{news.creationtime}}
                </li>
                <!-- <li>
                    <a href="">Taro.Yamada</a>    2019/05/02 18:32
                </li>
                <li>
                    <a href="">Chono</a>   2019/06/01 2019/05/01 20:13
                </li>
                <li>
                    <a href="">Tommy</a>   OB2019/05/04 14:3002019/04/24 13:32    
                </li> -->
            </ul>
        </div>
    </section>
</template>

<script>
export default {
    name : "Portal",
    
    data(){
        return {
            map         :   null,
            marker      :   [],
            infoWindow  :   [],

            newsData    :   []
        }
    },

    //  
    mounted(){
        console.log("info:portal")

        // populator
        this.newsData = []
        for(var i=0; i<this.$store.getters.portalNews.length; i++)
        {
            // populate
            var newsString = {
                userName : this.$store.getters.portalNews[i].userName,
                userUrl  : '/profile/' + this.$store.getters.portalNews[i].userNo,
                str      : "",
                creationtime : this.$store.getters.portalNews[i].creationtime
            }

            // 
            if(this.$store.getters.portalNews[i].type == "1")
            {
                newsString.str = "  " + this.$store.getters.portalNews[i].posName + " "
            }
            // 
            else if(this.$store.getters.portalNews[i].type == "2")
            {
                newsString.str = "" + this.$store.getters.portalNews[i].posName + "  " + this.$store.getters.portalNews[i].eventName
                var datetime = ""
                if(this.$store.getters.portalNews[i].time != null)
                {
                    datetime = "" + this.$store.getters.portalNews[i].date + " " + this.$store.getters.portalNews[i].time + ""
                    newsString.str = newsString.str + datetime
                }
                else
                {
                    datetime = "" + this.$store.getters.portalNews[i].date + ""
                    newsString.str = newsString.str + datetime
                }

                newsString.str = newsString.str + " "
            }

            console.log("info:news" +  newsString.str)
            this.newsData.push(newsString)
        }


        // return

        //  
        var mapLatLng = new google.maps.LatLng({lat: this.$store.getters.portalMarkers[0].lat, lng: this.$store.getters.portalMarkers[0].lng});
        this.map = new google.maps.Map(document.getElementById('map'), {
            center: mapLatLng,
            zoom: 7
        });

        // 
        for (var i = 0; i < this.$store.getters.portalMarkers.length; i++) {
            var markerLatLng = new google.maps.LatLng({lat: this.$store.getters.portalMarkers[i].lat, lng: this.$store.getters.portalMarkers[i].lng}); // 
            this.marker[i] = new google.maps.Marker({ // 
                position: markerLatLng, // 
                map: this.map // 
            });
    
            let profUrl = location.href + 'profile/' + this.$store.getters.portalMarkers[i].userNo

            this.infoWindow[i] = new google.maps.InfoWindow({ // 
                content: '<div class="mapInfo"><p class="markerTitle">' + this.$store.getters.portalMarkers[i].name + '</p><ul><li><a href="' +  profUrl + '">' + this.$store.getters.portalMarkers[i].userName +'</a> </li></ul></div>' // 
            });
    
            this.markerEvent(i); // 
        }
    },

    methods:{
        markerEvent(i) {
            var self = this
            this.marker[i].addListener('click', function() { // 
                self.infoWindow[i].open(self.map, self.marker[i]); // 
            });
        }
    }
}
</script>

<style scoped>
/*--  Start of map section  --*/
    #map {
        height: 500px;
        width: 100%;
    }

    #mapInfo{
        padding: 5px 10px 10px 10px;
        display: block;
    }

    #mapInfo li{
        font-size: 90%;
    }

    /* .markerTitle{
        color : #00a7ce;
        font-size : 120%;
    } */
/*--  End of map section  --*/
</style>



